import React, { useEffect, useState } from 'react';

export const Row = ({ item, metadata, onItemChange }) => {
  const [_id, setId] = useState(item.id);
  const [_item, setItem] = useState(item);

  useEffect(() => {
    setId(item.id);
    setItem(item);
  }, [item]);

  return (
    <tr>
      {Object.keys(metadata).map(key => {
        if (key !== 'id') {
          return (
            <td key={key}>
              {
                <input
                  className="form-control"
                  type={metadata[key].type}
                  value={item[key]}
                  onChange={event => {
                    onItemChange({
                      ...item,
                      [key]:
                        metadata[key].type === 'number'
                          ? Number(event.target.value)
                          : event.target.value,
                    });
                  }}
                />
              }
            </td>
          );
        }
      })}
    </tr>
  );
};
